import 'rc-tree-select/assets/index.less';
import React from 'react';
import ReactDOM from 'react-dom';
import 'rc-dialog/assets/index.css';
import TreeSelect from 'rc-tree-select';
import './demo.less';

const treeData = [{
  "key": "P001",
  "title": "P001",
  "value": 111,
  "children": [{
    "key": "0020",
    "title": "0020",
    "value": 222,
    "children": [{"key": "9459", "title": "9459", "value": 333}]
  }]
}, {
  "key": "P002",
  "title": "P002",
  "value": 444,
  "children": [{
    "key": "0021",
    "title": "0021",
    "value": 555,
    "children": [
      {"key": "9458", "title": "9458", "value": 666},
      {"key": 777, "title": 777, "value": 777},
      // {"key": 888, "title": 888, "value": 888},
      {"key": '888', "title": 'string 888', "value": '888'},
    ]
  }]
}];

function onChange(valueList) {
  console.log('onChange:', valueList);
}

class Demo extends React.Component {
  state = {
    value: [],
  };

  onChange = (value) => {
    console.log('onChange:', value);
    this.setState({ value });
  };

  render() {
    return <TreeSelect
      style={{width: 500}}
      treeCheckable
      treeDefaultExpandAll
      treeData={treeData}
      value={this.state.value}
      onChange={this.onChange}
    />
  }
}

ReactDOM.render(<Demo />, document.getElementById('__react-content'));



